body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Roboto", "Segoe UI", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url(/Wiki-Viewer-React/static/media/magnifier.ec9b99f8.jpg);
  background-size: cover;
  background-position: center;
  min-height: 100vh;
  background-attachment: fixed;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  height: 95vh;
  width: 100%;
  padding-top: 5vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

@media screen and (max-width: 650px) {
    .App  {
        height: 97vh;
        padding-top: 3vh;
    }
}

@media screen and (max-width: 450px) {
    .App  {
        height: 98.5vh;
        padding-top: 1.5vh;
    }
}

.SearchwikiPart {
  -webkit-flex-grow: 1;
          flex-grow: 1;
}

.errorMessage {
  display: none;
  background-color: rgba(187, 0, 0, 0.7);
  color: rgb(255, 248, 248);
  padding: 15px;
  font-size: 1.2rem;
  border-radius: 7px;
}


.Searchwiki h1 {
    font-size: 3rem;
    margin-top: 0;
}

@media screen and (max-width: 650px) {
    .Searchwiki h1  {
        font-size: 2.3rem;
    }
}

@media screen and (max-width: 450px) {
    .Searchwiki h1  {
        font-size: 1.8rem;
    }
}

.Searchwiki input {
    font-size: 1rem;
    padding: 3px 20px;
    width: 450px;
    max-width: 80%;
    border-radius: 5px;
}

@media screen and (max-width: 650px) {
    .Searchwiki input {
        font-size: 0.9rem;
        padding: 2px 15px;
        width: 300px;
        max-width: 90%;
        margin: 5px;
    }
}

@media screen and (max-width: 450px) {
    .Searchwiki input {
        padding: 2px 10px;
        width: 200px;
        margin: 2px;
    }
}

.btn {
    font-size: 1.1rem;
    font-family: inherit;
    border-radius: 5px;
    margin: 0.5rem;
    padding: 3px 20px;
}

@media screen and (max-width: 650px) {
    .btn {
        font-size: 1rem;
        font-family: inherit;
        padding: 3px 10px;
        margin-top: 0;
        margin-bottom: 0;
    }
}

@media screen and (max-width: 450px) {
    .btn {
        padding: 3px 5px;
    }
}

.btn:hover {
    text-decoration: underline;
}

.btn--search {
    background-color: rgb(59, 23, 0);
    color: rgb(255, 244, 234);
}

.btn--random {
    color: rgb(59, 23, 0);
}
.Searchresult {
    background-color: rgba(255, 255, 255, 0.8);
    width: 80%;
    margin: 10px auto;
    padding: 10px 20px;
    text-align: left;
    border-radius: 5px;
    box-shadow: 0 1.5px 4px rgba(black, 0.2);
}

@media screen and (max-width: 650px) {
    .Searchresult {
        width: 90%;
        padding: 5px 15px;
    }
}

@media screen and (max-width: 450px) {
    .Searchresult {
        padding: 5px 10px;
    }
}

.Searchresult:hover {
    background-color: rgba(221, 217, 209, 0.9);
}

.Searchresult a {
    text-decoration: none;
        color: rgb(59, 23, 0);
}

.Searchresult h2 {
    font-size: 1.2rem;
    margin: 0.3rem;
}

@media screen and (max-width: 650px) {
    .Searchresult h2 {
        font-size: 1.1rem;
        margin: 0.2rem;
    }
}

@media screen and (max-width: 450px) {
    .Searchresult h2 {
        font-size: 1rem;
        margin: 0.1rem;
    }
}

.Searchresult p {
    font-size: 1.1rem;
    line-height: 1.2;
    margin: 0.3rem;
}

@media screen and (max-width: 650px) {
    .Searchresult p {
        font-size: 1.1rem;
        line-height: 1.2;
        margin: 0.2rem;
}
}

@media screen and (max-width: 450px) {
    .Searchresult p {
        font-size: 1rem;
        margin: 0.1rem;
}
}
.Footer {
    -webkit-align-self: stretch;
            align-self: stretch;
    background-color: rgba(43, 43, 43, 0.9);
    color: rgb(230, 230, 230);
    font-size: 0.9rem;
    padding: 5px;   
    display: -webkit-flex;   
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

@media screen and (max-width: 650px) {
    .Footer {
    font-size: 0.8rem;
    padding: 5px;   
}
}

@media screen and (max-width: 450px) {
    .Footer {
    font-size: 0.7rem;
    padding: 5px;   
}
}

.Footer > * {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}

.Footer img {
    height: 2rem;
    width: 2rem;
    margin-left: 10px;
}

@media screen and (max-width: 650px) {
    .Footer img {
        height: 1.7rem;
        width: 1.7rem;
        margin-left: 8px;
    }
}

